thead {
  display: table;
  width: 100%;
  table-layout: fixed; /* even columns width , fix width of table too*/
}

.thClient,
.thTitle,
.thWorker,
.thStarted,
.thEnded,
.thDuration,
.thStatus {
  position: relative;
  height: 44.88px;
  background: #13ad83;
  min-width: 164.14px;
  color: #fafafa;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  text-transform: capitalize;
  text-indent: 28px;
  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 0;
  margin: 0px 0px;
  font-weight: 300;
}

.thClient {
  border-radius: 5px 0px 0px 0px;
}

.thStatus {
  border-radius: 0px 5px 0px 0px;
}

.filterIcons,
.clearFilterIcons {
  position: relative;
  margin-bottom: 2px;
  cursor: pointer;
}

.clearFilterIcons {
  margin-bottom: 5px;
}

.dropdownClient,
.dropdownTitle,
.dropdownWorker,
.dropdownDuration,
.dropdownStatus,
.dropdownStartDate,
.dropdownEndDate {
  position: absolute;
  background-color: #ffffff;
  color: #000000;
  bottom: 0;
  margin-left: 25px;
  border-radius: 4px;
  border: 1px solid #b1b1b1;
  width: 232px;
  z-index: 1;
  box-shadow: 0px 3px 5px #c5c5c5;
}

.dropdownClient {
  height: 250px;
  margin-bottom: -252px;
}

.dropdownTitle,
.dropdownWorker,
.dropdownDuration {
  height: 183px;
  margin-bottom: -185px;
}

.dropdownStatus {
  height: 238px;
  margin-bottom: -240px;
  width: 175px;
  right: 0;
  margin-right: 50px;
}

.dropdownDuration {
  width: 194px;
  right: 0;
}

.dropdownStartDate,
.dropdownEndDate {
  right: 0;
  margin-right: 0px;
  height: 298px;
  width: 850px;
  margin-bottom: -300px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.activeDataView {
  width: 228.5px !important;
}

@media (max-width: 1440px) {
  .thTitle,
  .thWorker,
  .thStarted,
  .thEnded {
    width: 202px !important;
  }
}

@media (max-width: 1980px) {
  .thClient {
    width: 106px;
  }

  .thTitle,
  .thWorker,
  .thStarted,
  .thEnded {
    width: 262.25px;
  }

  .thDuration {
    width: 98.44px;
  }

  .thStatus {
    width: 122px;
  }
}

@media (min-height: 900px) {
  td {
    height: 56.08px !important;
  }
}
