.header {
  position: relative;
  width: 100%;
}

.closeButton {
  content: url('../../../../../../assets/icons/Close.png');
  position: absolute;
  right: 0;
  cursor: pointer;
}

.tabs {
  display: flex;
  margin-top: 30px;
}

.tabItem {
  border-bottom: 1px solid #dee2e6;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: #6d6d73;
  width: 107px;
  height: 25px;
  padding-top: 5px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  cursor: pointer;
}

.activeTabItem {
  color: #13ad83;
  border: 1px solid #dee2e6;
  border-bottom: none;
}
