.td {
  white-space: nowrap;
  overflow: hidden;
  width: 10%;
}

.activeRow > td {
  background-color: #116c53;
  color: #fafafa;
  border: none;
}

@media (max-width: 1440px) {
  .tdLastName,
  .tdEmail,
  .tdRole,
  .tdDateCreated {
    width: 202px !important;
  }
}

@media (max-width: 1980px) {
  .tdFirstName,
  .tdLastName,
  .tdRole,
  .tdDateCreated {
    width: 212.75px;
  }

  .tdEmail {
    width: 277px;
  }

  .tdLastSeen {
    width: 127px;
  }
}

@media (min-height: 900px) {
  td {
    height: 56.08px !important;
  }
}
