.tableFooter {
  background: #ffffff;
  border-radius: 0px 0px 5px 5px;
  width: 100%;
  height: 64px;
  padding: 17px 20px;
  border-top: 1px solid #e2e8f0;
}

.label {
  font-size: 12px;
  line-height: 14px;
  color: #656565;
  margin-right: 10px;
}

.chevLeftIcon,
.chevRightIcon {
  width: 4.38px;
  height: 7.02px;
  margin-top: -1px;
}

.chevLeftIcon {
  transform: rotate(180deg);
}

.listSelection {
  appearance: none;
  background: #008662;
  border-radius: 4px;
  padding: 5px 0px 5px 0px;
  text-indent: -16px;
  color: #fafafa;
  font-size: 12px;
  line-height: 14px;
  border: none;
  margin-right: 10px;
  outline: none;
  cursor: pointer;
  width: 50px;
  position: relative;
}

.listSelection:after {
  position: absolute;
  right: 0;
  margin-right: 5px;
  top: 0;
  margin-top: 4px;
  content: url('../../../../../assets/icons//Triangle-Down.png');
}

.listSelection option {
  color: #000000;
  background-color: #ffffff;
}

.exportButton {
  background: #008662;
  width: 86px;
  height: 24px;
  border-radius: 4px;
  border: none;
  color: #fafafa;
  font-size: 12px;
  line-height: 14px;
  float: right;
}

.pageSizeOptions {
  position: absolute;
  height: 100px;
  width: 50px;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  border-bottom: none;
  top: 0;
  right: 0;
  margin-right: 10px;
  margin-top: -99px;
}

.optionItem {
  height: 25px;
  text-align: center;
  font-size: 12px;
  cursor: pointer;
}

.optionItem:hover {
  background-color: #ced4da;
}
