.body {
  position: relative;
  margin-top: 30px;
  width: 100%;
}

.title {
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #6d6d73;
  width: 200px;
}

.runningStatus,
.successStatus,
.stoppedStatus,
.failedStatus,
.queuedStatus {
  box-sizing: border-box;
  backdrop-filter: blur(10px);
  border-radius: 2px;
  font-size: 10px;
  line-height: 12px;
  text-transform: capitalize;
  color: #6d6d73;
  width: 81px;
  height: 18px;
  text-indent: 0px;
  margin-left: 28px;
  text-align: center;
  padding-top: 1.5px;
  border: 1px solid transparent;
  width: 81px;
  height: 18px;
  position: absolute;
  right: 0;
  top: 0;
}

.runningStatus {
  background-color: #bdd7ee;
  color: #297fcb;
}

.successStatus {
  background-color: #88d6c1;
  color: #127056;
}

.stoppedStatus {
  background-color: #bfbfbf;
  color: #5c5c5c;
}

.failedStatus {
  background-color: #f4b183;
  color: #c0550b;
}

.queuedStatus {
  background-color: #ffd966;
  color: #a48c43;
}

.detailLabel {
  font-size: 12px;
  line-height: 14px;
  color: #b1b1b1;
}

.detailText {
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  color: #6d6d73;
}
