.tbodyDiv {
  height: 538.56px;
}

tbody {
  display: block;
}

tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}

tr {
  border-bottom: solid 1px #e2e8f0 !important;
}

.activeRow > td {
  background-color: #116c53;
  color: #fafafa;
  border: none;
}

.tdClient,
.tdTitle,
.tdWorker,
.tdStarted,
.tdEnded,
.tdDuration,
.tdStatus {
  overflow-x: hidden;
}

td {
  width: 164.14px;
  height: 44.88px;
  background: #ffffff;
  flex: none;
  order: 1;
  align-self: stretch;
  flex-grow: 0;
  margin: 0px 0px;
  text-indent: 28px;
  white-space: nowrap;
  font-size: 13px;
  line-height: 16px;
  text-transform: capitalize;
  color: #656565;
}

.runningStatus,
.successStatus,
.stoppedStatus,
.failedStatus,
.queuedStatus {
  box-sizing: border-box;
  backdrop-filter: blur(10px);
  border-radius: 2px;
  font-size: 12px;
  line-height: 14px;
  text-transform: capitalize;
  color: #6d6d73;
  width: 81px;
  height: 18px;
  text-indent: 0px;
  margin-left: 28px;
  text-align: center;
  padding-top: 1.5px;
  border: 1px solid transparent;
}

.runningStatus {
  background-color: #bdd7ee;
  color: #297fcb;
}

.successStatus {
  background-color: #88d6c1;
  color: #127056;
}

.stoppedStatus {
  background-color: #bfbfbf;
  color: #5c5c5c;
}

.failedStatus {
  background-color: #f4b183;
  color: #c0550b;
}

.queuedStatus {
  background-color: #ffd966;
  color: #a48c43;
}

.activeRow > td > div {
  border-color: #fafafa !important;
  color: #fafafa;
  background-color: transparent !important;
}

.activeDataView {
  width: 228.5px !important;
}

@media (max-width: 1980px) {
  .tdClient {
    width: 106px;
  }

  .tdTitle,
  .tdWorker,
  .tdStarted,
  .tdEnded {
    width: 262.25px;
  }

  .tdDuration {
    width: 98.44px;
  }

  .tdStatus {
    width: 122px;
  }
}

@media (max-width: 1440px) {
  .tdTitle,
  .tdWorker,
  .tdStarted,
  .tdEnded {
    width: 202px !important;
  }
}

@media (min-height: 900px) {
  td {
    height: 56.08px !important;
  }

  .tbodyDiv {
    height: 672.96px !important;
  }
}
