.dropdownSearch {
  margin-top: 15px;
  margin-left: -10px;
  width: 195px;
  padding: 9px 7px;
  border-radius: 4px;
  border: 1px solid #ced4da;
  color: #b1b1b1;
}

.dropdownSearch::placeholder {
  color: #b1b1b1;
}

.dropdownSearch:focus {
  outline: none;
}

.clientList {
  padding: 10px 22px;
  padding-bottom: 0px;
  margin-top: 5px;
}

.item {
  width: 100%;
  height: 20px;
  display: flex;
  padding: 3px 3px;
}

.item:hover {
  background-color: #ced4da;
}

.checkbox {
  width: 13px;
  height: 13px;
  border: 1px solid #dee2e6;
  border-radius: 2px;
  cursor: pointer;
}

.clearFilterButton {
  color: #6d6d73;
  font-size: 12px;
  line-height: 14px;
  cursor: pointer;
  margin-left: 55px;
  margin-top: 11px;
}

.selectedClient {
  position: relative;
  background-color: #13ad83;
  border-color: #13ad83;
}

.checkIcon {
  position: absolute;
  left: 0;
  margin-top: 2px;
  margin-left: 1px;
}
