.vaiHeader {
  padding-top: 15px;
  padding-bottom: 5px;
  display: flex;
}

.vaiHeaderText {
  font-weight: 300;
  font-size: 30px;
  line-height: 36px;
  color: #000000;
}

.vaiHeaderSearch {
  padding: 3px 10px;
  width: 336px;
  height: 38px;
  background: #ffffff;
  color: #b1b1b1;
  border: 1px solid #ced4da;
  border-right: none;
  box-sizing: border-box;
  border-radius: 4px;
  font-size: 12px;
  line-height: 14px;
  outline: none;
  box-shadow: none;
}

.vaiSearchIcon {
  border: 1px solid #ced4da;
  border-left: none;
  height: 38px;
  border-radius: 4px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 3px;
  background: #ffffff;
}

.newUserAddButton {
  color: #ffffff;
  font-size: 12px;
  line-height: 14px;
  text-transform: capitalize;
  background: #008662;
  border-radius: 4px;
  height: 36px;
  border: none;
  width: 103px;
  border-radius: 4px;
}
