thead {
  display: table;
  width: 100%;
  table-layout: fixed;
}

.thFirstName,
.thLastName,
.thEmail,
.thRole,
.thDateCreated,
.thLastSeen {
  position: relative;
  height: 44.88px;
  background: #008662;
  min-width: 164.14px;
  color: #fafafa;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  text-transform: capitalize;
  text-indent: 28px;
  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 0;
  margin: 0px 0px;
  font-weight: 300;
}

.thFirstName {
  border-radius: 5px 0px 0px 0px;
}

.thLastSeen {
  border-radius: 0px 5px 0px 0px;
}

.filterIcons,
.clearFilterIcons {
  position: relative;
  margin-bottom: 2px;
  cursor: pointer;
}

.clearFilterIcons {
  margin-bottom: 5px;
}

@media (max-width: 1440px) {
  .thLastName,
  .thEmail,
  .thRole,
  .thDateCreated {
    width: 202px !important;
  }
}

@media (max-width: 1980px) {
  .thFirstName,
  .thLastName,
  .thRole,
  .thDateCreated {
    width: 212.75px;
  }

  .thEmail {
    width: 277px;
  }

  .thLastSeen {
    width: 127px;
  }
}

@media (min-height: 900px) {
  td {
    height: 56.08px !important;
  }
}
