.closeButton {
  content: url('../../../../assets/icons/Close.png');
  position: absolute;
  right: 0;
  margin-right: 20px;
  cursor: pointer;
}

.title {
  font-weight: 300;
  font-size: 30px;
  line-height: 36px;
  margin-top: 25px;
}

.inputForm {
  margin-top: 10px;
}

.inputLabel {
  font-size: 14px;
  line-height: 17px;
  color: #343a40;
  margin-bottom: 5px;
}

.inputField {
  border: 1px solid #b1b1b1;
  border-radius: 4px;
  width: 100%;
  font-size: 12px;
  line-height: 14px;
  color: #b1b1b1;
  text-indent: 10px;
  padding-top: 8px;
  padding-bottom: 8px;
}

.inputField:focus {
  outline: none;
}

.inputFieldPasswordStrength1,
.inputFieldPasswordStrength2,
.inputFieldPasswordStrength3,
.inputFieldPasswordStrength4 {
  width: 25%;
  height: 2px;
  background: #b1b1b1;
  margin-right: 5px;
  margin-top: 5px;
}

.inputFieldPasswordStrength4 {
  margin-right: 0px;
}

.activePasswordStrength {
  background-color: #008662;
}

.passwordStrengthText {
  font-size: 12px;
  line-height: 14px;
  text-align: right;
  color: #b1b1b1;
  position: absolute;
  right: 0;
  margin-right: 17px;
  margin-top: 5px;
}

.selectForm:nth-child(1) {
  margin-right: 50px;
}

.selectForm {
  margin-top: 10px;
}

.selectLabel {
  font-size: 14px;
  line-height: 17px;
  color: #343a40;
}

.selectButton {
  background: #008662;
  border-radius: 4px;
  padding: 9px 12px;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
  border: none;
}

.selectButton:after {
  content: url('../../../../assets/icons//Triangle-Down.png');
  margin-left: 8px;
}

.footer {
  position: absolute;
  width: 100%;
  bottom: 0;
  margin-bottom: 15px;
  text-align: center;
}

.saveIcon,
.circleCloseIcon {
  cursor: pointer;
}

.saveIcon {
  margin-left: -18px;
  margin-right: 18px;
}

.circleCloseIcon {
  margin-left: 18px;
}
