.timeRangeButton {
  position: relative;
  width: 100%;
  height: 32px;
  margin-left: -28px;
  border: none;
  color: #ffffff;
  background: #13ad83;
  border-radius: 4px;
  text-align: left;
  padding-left: 15px;
  padding-right: 15px;
}

.timeRangeButton:after {
  position: absolute;
  right: 0;
  margin-right: 15px;
  content: url('../../../../../../assets/icons/Triangle-Down.png');
}

.hoursInput,
.minutesInput,
.secondsInput {
  width: 48px;
  height: 32px;
  padding: 5px 13px;
  border-radius: 4px;
  border: 1px solid #ced4da;
  color: #b1b1b1;
  margin-right: 7.5px;
}

.hoursInput::placeholder,
.minutesInput::placeholder,
.secondsInput::placeholder {
  color: #b1b1b1;
}

.hoursInput:focus,
.minutesInput:focus,
.secondsInput:focus {
  outline: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type='number'] {
  -moz-appearance: textfield;
}

.clearFilterButton {
  color: #6d6d73;
  font-size: 12px;
  line-height: 14px;
  cursor: pointer;
  margin-left: 5px;
  margin-top: 11px;
}

.options {
  position: absolute;
  bottom: 0;
  left: 0;
  margin-bottom: -73px;
  height: 75px;
  width: 100%;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  z-index: 2;
}

.optionItem {
  height: 25px;
  width: 100%;
  color: #000000;
  font-size: 12px;
  padding-top: 6.5px;
  padding-bottom: 6.5px;
  cursor: pointer;
}

.optionItem:hover {
  background-color: #ced4da;
}

.optionItem span {
  margin-left: -15px;
}
