.checkbox {
  margin-top: 5px;
  margin-bottom: 5px;
  width: 13px;
  height: 13px;
  background: #ffffff;
  border: 1px solid #dee2e6;
  border-radius: 2px;
  cursor: pointer;
}

.item span {
  margin-top: 5px;
  margin-left: -15px;
}

.runningStatus,
.queuedStatus,
.successStatus,
.failedStatus,
.stoppedStatus {
  margin-top: 3px;
  margin-left: 14px;
  width: 81px;
  height: 18px;
  border-radius: 2px;
  font-weight: 500;
  font-size: 10px;
  text-transform: capitalize;
  padding-top: 2px;
}

.selectAllStatus {
  margin-top: 5px;
}

.runningStatus span,
.queuedStatus span,
.successStatus span,
.stoppedStatus span {
  margin-left: -5px;
}

.failedStatus span {
  margin-left: 0px;
}

.runningStatus {
  color: #297fcb;
  background-color: #bdd7ee;
}

.queuedStatus {
  color: #a48c43;
  background-color: #ffd966;
}

.successStatus {
  color: #127056;
  background-color: #88d6c1;
}

.failedStatus {
  color: #c0550b;
  background-color: #f4b183;
}

.stoppedStatus {
  color: #5c5c5c;
  background-color: #bfbfbf;
}

.clearFilterButton {
  color: #6d6d73;
  font-size: 12px;
  line-height: 14px;
  cursor: pointer;
  margin-left: -12px;
  margin-top: 11px;
}

.selectedStatus {
  position: relative;
  background-color: #13ad83;
  border-color: #13ad83;
}

.checkIcon {
  position: absolute;
  left: 0;
  margin-top: 2px;
  margin-left: 1px;
}
