.table {
  box-shadow: 0px 15px 50px rgba(0, 0, 0, 0.15);
}

.tableDiv {
  overflow-x: auto;
  overflow-y: hidden;
  height: 730px;
}

.dataViewPanel {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 15px 17px;
  width: 364px;
  height: 794px;
  border: 2px solid #13ad83;
  box-sizing: border-box;
  box-shadow: 0px 15px 50px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
}
