.body {
  position: relative;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  min-height: 700px;
}

.panel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: -10px;
  background-color: #fafafa;
  height: 780px;
  width: 520px;
  border-radius: 10px;
}

.virtualAiLogo {
  height: 60px;
  width: auto;
}

.title {
  color: #6d6d73;
  font-weight: 300;
  font-size: 30px;
  line-height: 36px;
}

.description {
  font-weight: 400;
  font-size: 15px;
  line-height: 17.9px;
  letter-spacing: 3%;
  width: 390px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 40px;
}

.emailInput {
  font-size: 14px;
  line-height: 16.71px;
  font-weight: 400;
  color: #6d6d73;
  background: #fafafa;
  width: 389px;
  height: 33px;
  padding: 8px 15px 8px 5px;
  margin-top: 30px;
  margin-left: 62px;
  box-shadow: inset 0px -1px 0px #6d6d73;
  border: none;
  outline: none;
}

.resetPasswordButton {
  margin-top: 40px;
  font-size: 18px;
  font-weight: 100;
  line-height: 21px;
  color: #ffffff;
  background-color: #13ad83;
  width: 390px;
  height: 43px;
  padding: 11px 24px;
  margin-left: 63px;
  border-radius: 4px;
  border: none;
}

.redirectUrlText {
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.03em;
  color: #6d6d73;
  margin-top: 15px;
}

@media (min-width: 515px) {
  .panel {
    height: 530px;
  }
}

@media (max-width: 515px) {
  .panel {
    padding-top: 50px;
  }
}
